import * as React from 'react';
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import LinearProgress, { linearProgressClasses } from '@mui/material/LinearProgress';
import { useGetDataQuery } from '../redux'
import style from '../styles/Main.module.scss'

const BorderLinearProgress = styled(LinearProgress)(({ theme }) => ({
  height: 10,
  borderRadius: 5,
  [`&.${linearProgressClasses.colorPrimary}`]: {
    backgroundColor: theme.palette.grey[theme.palette.mode === 'light' ? 200 : 800],
  },
  [`& .${linearProgressClasses.bar}`]: {
    borderRadius: 5,
    backgroundColor: theme.palette.mode === 'light' ? '#1a90ff' : '#308fe8',
  },
}));

// Inspired by the former Facebook spinners.
function FacebookCircularProgress(props) {
  return (
    <Box sx={{ position: 'relative' }}>
    
    </Box>
  );
}

export default function CustomizedProgressBars() {
    const{data = [], isLoading} = useGetDataQuery()
 if(isLoading) return  <h1>1</h1>
  return (
    <div className={style.langCont}>
    {data.map(item =>(
   
       <p key='waka_948ffef7-3d05-490e-a9ce-4d998b9745e0' style={{color: item.color}}>
        {item.name} 
    
    <Box sx={{ flexGrow: 1 }}>
      <FacebookCircularProgress />
      <br />
      <BorderLinearProgress variant="determinate" value={item.percent} />
      <span>{item.percent}%</span>
    </Box>
      </p>
    ))}
  </div>
   
  );
}